<template>
  <v-expansion-panel class="expansion-pl ext__form">
    <v-expansion-panel-content hide-actions value="1">
      <div slot="header"><div class="background-triangle-small"> <v-icon size="18" color="white">star_rate</v-icon> </div>THNG TIN CHUNG H S</div>
      <v-card>
        <v-card-text class="py-0">
          <v-layout wrap class="px-2 py-2">
          <v-flex xs12 sm4 class="pr-3">
            <div class="xs12 sm12 pb-1">
              <span class="pr-2">Ch h s: </span>
              <span class="pl-0 text-bold"> {{thongTinChiTietHoSo.applicantName}}</span>
            </div>
            <!--  -->
            <div class="xs12 sm12 pb-1 overHidden">
              <span class="pr-2">a ch: </span>
              <v-tooltip top>
                <span slot="activator" class="text-bold ">{{ thongTinChiTietHoSo.address }}</span>
                <span class="pl-0"> {{thongTinChiTietHoSo.address}} </span>
              </v-tooltip>
            </div>
            <!--  -->
            <div class="xs12 sm12 pb-1">
              <span class="pr-2">M h s: </span>
              <span class="pl-0 text-bold ">  {{thongTinChiTietHoSo.dossierNo}} </span>
            </div>
            <!--  -->
            <div class="xs12 sm12 pb-1">
              <span class="pr-2">Trng thi: </span>
              <span class="pl-0 text-bold "> {{thongTinChiTietHoSo.dossierStatusText}} </span>
            </div>
          </v-flex>
          <!--  -->
          <v-flex xs12 sm4>
            <div class="xs12 sm12 pb-1">
              <span class="pr-2">Ngy gi trc tuyn: </span>
              <span class="pl-0 text-bold" v-if="thongTinChiTietHoSo.online"> {{thongTinChiTietHoSo.submitDate}} </span>
              <span class="pl-0 text-bold" v-else> Mt ca </span>
            </div>
            <!--  -->
            <div class="xs12 sm12 pb-1">
              <span class="pr-2">Ngy tip nhn: </span>
              <span class="pl-0 text-bold "> {{thongTinChiTietHoSo.receiveDate}}</span>
            </div>
            <!--  -->
            <div class="xs12 sm12 pb-1">
              <span class="pr-2">Thi hn quy nh: </span>
              <span class="pl-0 text-bold "> 
                {{durationText(thongTinChiTietHoSo.durationUnit, thongTinChiTietHoSo.durationCount)}} lm vic 
              </span>
            </div>
            <!--  -->
            <div class="xs12 sm12 pb-1">
              <span class="pr-2">Ngy hn tr: </span>
              <span class="pl-0 text-bold "> {{thongTinChiTietHoSo.dueDate}}</span>
            </div>
          </v-flex>
          <!--  -->
          <v-flex xs12 sm4>
            <div class="xs12 sm12 pb-1" style="color:#0b72ba">Thng tin lin h:</div>
            <div class="xs12 sm12 pb-1">
              <span class="pl-0">Th in t: </span>
              <span class="pl-0 text-bold "> {{thongTinChiTietHoSo.contactEmail}} </span>
            </div>
            <!--  -->
            <div class="xs12 sm12 pb-1">
              <span class="pr-2">in thoi: </span>
              <span class="pl-0 text-bold "> {{thongTinChiTietHoSo.contactTelNo}} </span>
            </div>
          </v-flex>
        </v-layout>
        </v-card-text>
      </v-card>
    </v-expansion-panel-content>
  </v-expansion-panel>
</template>
<script>
  export default {
    props: {
      detailDossier: {
        type: Object,
        default: () => {}
      }
    },
    watch: {
      detailDossier (val) {
        console.log('watch', val)
        var vm = this
        vm.thongTinChiTietHoSo = val
      }
    },
    components: {
    },
    data: () => ({
      thongTinChiTietHoSo: {},
      showContactDetail: false
    }),
    computed: {
      loading () {
        return this.$store.getters.loading
      }
    },
    created () {
      var vm = this
      vm.thongTinChiTietHoSo = vm.detailDossier
    },
    mounted () {
      var vm = this
      vm.thongTinChiTietHoSo = vm.detailDossier
    },
    methods: {
      initData (data) {
        var vm = this
        vm.$store.dispatch('getDetailDossier', data).then(resultDossier => {
          vm.thongTinChiTietHoSo = resultDossier
          console.log('thongTinChiTietHoSo', vm.thongTinChiTietHoSo)
        })
      },
      goBack () {
        window.history.back()
      },
      durationText (durationUnit, durationCount) {
        var durationText
        if (durationUnit === 1 && durationCount > 8) {
          let day = Math.floor(durationCount / 8) + ' ngy'
          let hours
          if (durationCount % 8 !== 0) {
            hours = ((durationCount / 8) - Math.floor(durationCount / 8)) * 8 + ' gi'
          } else {
            hours = ''
          }
          durationText = `${day} ${hours}`
        } else if (durationUnit === 0) {
          durationText = durationCount + ' ngy'
        } else if (durationUnit === 1 && durationCount <= 8) {
          durationText = durationCount + ' gi'
        }
        return durationText
      }
    },
    filters: {
      dateTimeView (arg) {
        if (arg) {
          let value = new Date(arg)
          return `${value.getDate().toString().padStart(2, '0')}/${(value.getMonth() + 1).toString().padStart(2, '0')}/${value.getFullYear()} | ${value.getHours().toString().padStart(2, '0')}:${value.getMinutes().toString().padStart(2, '0')}`
        } else {
          return ''
        }
      }
    }
  }
</script>
<style>
  .overHidden {
    overflow: hidden;
    text-overflow: ellipsis; 
    white-space: nowrap
  }
</style>




